.pager__items {
 clear: both;
 text-align: center;

 .pager__item {
   display: inline;
   padding: .5em;

   a {
     background-color: transparent;
     color: black;
     text-decoration: none;

     &:hover {
       color: gray;
     }
   }

   &.is-active {

     a {
       color: red;
       text-decoration: underline;
     }
   }
 }
}

.visually-hidden {
 clip: rect(1px, 1px, 1px, 1px);
 height: 1px;
 overflow: hidden;
 position: absolute;
 width: 1px;
 word-wrap: normal;
}
